import {type IEvtMng, argChk_Boolean, argChk_Num} from './CmnLib';
import type {IHTag, HArg} from './Grammar';
import type {IVariable, IMain, INoticeChgVolume} from './CmnInterface';
import type {Config} from './Config';
import {BUF_BGM, BUF_SE, type HSndBuf, SndBuf} from './SndBuf';

import {sound, utils} from '@pixi/sound';


export class SoundMng {
	#hSndBuf	: HSndBuf	= {};

	constructor(cfg: Config, hTag: IHTag, private readonly val: IVariable, main: IMain) {
		hTag.volume		= o=> this.#volume(o);		// 
		hTag.fadebgm	= o=> this.#fadebgm(o);		// BGM
		hTag.fadeoutbgm	= o=> this.#fadeoutbgm(o);	// BGM
		hTag.fadeoutse	= o=> this.#fadeoutse(o);	// 
		hTag.fadese		= o=> this.#fadese(o);		// 
		hTag.playbgm	= o=> this.#playbgm(o);		// BGM 
		hTag.playse		= o=> this.#playse(o);		// 
		hTag.stop_allse	= ()=> this.#stop_allse();	// 
		hTag.stopbgm	= o=> this.#stopbgm(o);		// BGM 
		hTag.stopse		= o=> this.#stopse(o);		// 
		hTag.wb			= o=> this.#wb(o);			// BGM 
		hTag.wf			= o=> this.#wf(o);			// 
		hTag.stopfadese	= o=> this.#stopfadese(o);	// 
		hTag.wl			= o=> this.#wl(o);			// BGM 
		hTag.ws			= o=> this.#ws(o);			// 
		hTag.xchgbuf	= o=> this.#xchgbuf(o);		// 

		val.setVal_Nochk('save', 'const.sn.loopPlaying', '{}');

		val.setVal_Nochk('tmp', 'const.sn.sound.codecs', JSON.stringify(utils.supported));

		SndBuf.init(cfg, val, main, this.#hSndBuf);
		sound.disableAutoPause = true;
	}

	#evtMng	: IEvtMng;
	setEvtMng(evtMng: IEvtMng) {this.#evtMng = evtMng; SndBuf.setEvtMng(evtMng)}
	setNoticeChgVolume(setGlbVol: INoticeChgVolume, setMovVol: INoticeChgVolume) {
		this.val.defValTrg('sys:sn.sound.global_volume', (_name: string, val: any)=> setGlbVol(sound.volumeAll = Number(val)));
		this.val.defValTrg('sys:sn.sound.movie_volume', (_name: string, val: any)=> setMovVol(Number(val)));

		// 
		this.val.setVal_Nochk('sys', 'sn.sound.global_volume', this.val.getVal('sys:sn.sound.global_volume', 1));
		this.val.setVal_Nochk('sys', 'sn.sound.movie_volume', this.val.getVal('sys:sn.sound.movie_volume', 1));
	}

	//MARK: 
	#volume(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		const vnV = 'const.sn.sound.'+ buf +'.volume';
		const arg_vol = this.#getVol(hArg, 1);
		if (Number(this.val.getVal('sys:'+ vnV)) === arg_vol) return false;

		this.val.setVal_Nochk('sys', vnV, arg_vol)	// sys:
		this.val.flush();	// fadese()

		// 
		hArg.time = 0;
		hArg.volume = Number(this.val.getVal('save:'+ vnV));	// save:
		return this.#fadese(hArg);
	}
	#getVol(hArg: HArg, def: number) {
		const vol = argChk_Num(hArg, 'volume', def);
		if (vol < 0) return 0;
		if (vol > 1) return 1;
		return vol;
	}

	//MARK: BGM/load
	#fadeoutbgm(hArg: HArg) {hArg.volume = 0; return this.#fadebgm(hArg)}
	//MARK: load
	#fadeoutse(hArg: HArg) {hArg.volume = 0; return this.#fadese(hArg)}
	//MARK: BGMload
	#fadebgm(hArg: HArg) {hArg.buf = BUF_BGM; return this.#fadese(hArg)}
	//MARK: 
	#fadese(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		this.#stopfadese(hArg);
		this.#hSndBuf[buf]?.fade(hArg);

		return false;
	}

	//MARK: BGM 
	#playbgm(hArg: HArg) {
		hArg.buf = BUF_BGM;
		hArg.canskip = false;
		argChk_Boolean(hArg, 'loop', true);
		return this.#playse(hArg);
	}

	//MARK: 
	#playse(hArg: HArg) {
		const {buf = BUF_SE, fn} = hArg;
		this.#stopse({buf});
		if (! fn) throw `fn buf:${buf}`;

		// 
		// const sb0 = this.#hSndBuf[buf];
		// const alias = ':snd:'+ fn;
		// if (sb0 && sb0.fn !== fn && ! this.val.getVal('tmp:const.sn.isPaging') && Assets.cache.has(alias)) Assets.unload(alias);

		// isSkipKeyDown()
		if (argChk_Boolean(hArg, 'canskip', true) && this.#evtMng.isSkipping) return false;

		const sb = this.#hSndBuf[buf] = new SndBuf(hArg, buf, fn);
		return sb.needLoad;
	}

	clearCache() {sound.removeAll()}

	//MARK: 
	#stop_allse() {
		for (const buf of Object.keys(this.#hSndBuf)) this.#stopse({buf});
		this.#hSndBuf = {};

		sound.stopAll();	// 

		return false;
	}
	//MARK: BGM load
	#stopbgm(hArg: HArg) {hArg.buf = BUF_BGM; return this.#stopse(hArg)}
	//MARK: 
	#stopse(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		this.#hSndBuf[buf]?.stopse(hArg);

		return false;
	}

	//MARK: BGM 
	#wb(hArg: HArg) {hArg.buf = BUF_BGM; return this.#wf(hArg)}

	//MARK: 
	#wf(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		return this.#hSndBuf[buf]?.wf(hArg) as boolean;
	}

	//MARK: 
	#stopfadese(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		this.#hSndBuf[buf]?.stopfadese(hArg);

		return false;
	}

	//MARK: BGM 
	#wl(hArg: HArg) {hArg.buf = BUF_BGM; return this.#ws(hArg)}
	//MARK: 
	#ws(hArg: HArg) {
		const {buf = BUF_SE} = hArg;
		return this.#hSndBuf[buf]?.ws(hArg) as boolean;
	}

	//MARK: 
	#xchgbuf(hArg: HArg) {
		const {buf: buf1 = BUF_SE, buf2 = BUF_SE} = hArg;
		if (buf1 === buf2) return false;

		const a = this.#hSndBuf[buf1];	//  noUncheckedIndexedAccess 
		const b = this.#hSndBuf[buf2];
		if (a) this.#hSndBuf[buf2] = a; else delete this.#hSndBuf[buf2];
		if (b) this.#hSndBuf[buf1] = b; else delete this.#hSndBuf[buf1];

		SndBuf.xchgbuf(hArg);

		return false;
	}

	//MARK: BGM
	playLoopFromSaveObj(all_stop_and_play: boolean): Promise<void>[] {
		const lp = String(this.val.getVal('save:const.sn.loopPlaying', '{}'));
		if (lp === '{}') {this.#stop_allse(); this.clearCache(); return []}
/*
					(Now)#hSndBuf
					stop	play
	hSaveLP	stop	-		stop		--[1]
	(to)	play	play	stop/play	--[2]
			eq play	play	-			--[2]
*/
		const hSaveLP: {[buf: string]: string} = JSON.parse(lp);
		if (all_stop_and_play) {this.#stop_allse(); this.clearCache()}
		else for (const [buf, sb] of Object.entries(this.#hSndBuf)) {
			// [1] #hSndBuf hSaveLP  buf -> stop
			if (! (buf in hSaveLP)) sb?.stopse({buf});
		}

		// [2] hSaveLP#hSndBuf
		return Object.entries(hSaveLP).map(([buf, fn])=> new Promise(re=> {
			const sb = this.#hSndBuf[buf]
			if (! all_stop_and_play && sb) {
				if (sb.fn === fn) {re(); return}
				//sb.stopse({buf});	//  fn !==  fn  stop
					// #playbgm()#playse()  stop 
			}

			const vm = 'save:const.sn.sound.'+ buf +'.';
			const hArg = {
				fn,
				buf,
				join	: false,
				loop	: true,
				volume	: Number(this.val.getVal(vm +'volume')),
				start_ms: Number(this.val.getVal(vm +'start_ms')),
				end_ms	: Number(this.val.getVal(vm +'end_ms')),
				ret_ms	: Number(this.val.getVal(vm +'ret_ms')),
				fnc		: re,	// loaded
			};
			if (hArg.buf === BUF_BGM) this.#playbgm(hArg);
			else this.#playse(hArg);
		}));
	}

	destroy() {this.#stop_allse(); this.clearCache()}

}
